import Slider from "react-slick";
import { BsChevronLeft, BsChevronRight } from "react-icons/bs";
import React, { useState } from "react";
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import './CSS/ProjectsSection.css'

const ProjectsSection = () => {
  var [selectedProject, setSelectedProject] = useState(1);

  function importAll(r) {
    let images = {};
    r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); return null });
    return images;
  }

  const NextArrow = ({ onClick }) => {
    return (<div className='nextArrow' onClick={onClick}><BsChevronRight /></div>);
  };

  const PrevArrow = ({ onClick }) => {
    return (<div className='prevArrow' onClick={onClick}><BsChevronLeft/></div>);
  };

  const ImageSlider = ({ images, slidesToShow = 3 }) => {
    const [imageIndex, setImageIndex] = useState(0);
    const settings = {centerMode: true, infinite: true, dots: false, speed: 300, slidesToShow: slidesToShow, centerPadding: "0", swipeToSlide: true, focusOnSelect: true,
    nextArrow: <NextArrow onClick/>,
    prevArrow: <PrevArrow onClick/>,
    beforeChange: (current, next) => setImageIndex(next),
    responsive: [
      {breakpoint: 1490, settings: {slidesToShow: 3, slidesToScroll: 1},},
      {breakpoint: 820, settings: {slidesToShow: 1, slidesToScroll: 1},}
    ]
  };

    const templateImages = images.map((image, idx) => {
      return (
        <div className={idx === imageIndex ? "activeSlide" : "slide"} key={image}>
          <div className="slideWrapper">
            {image.code ? image.code : <img src={image} alt={image} />}
          </div>
        </div>
      );
    });

    return <Slider {...settings}>{templateImages}</Slider>;
  };
  
  const images = importAll(require.context('../Assets', false, /\.(png|jpe?g|svg)$/));


  const ProjectDetail = (props) => {

    const allProjectMockups = [
      [],
      [
        "Aqua - Mockup 1.png", 
        "Aqua - Mockup 2.png", 
        "Aqua - Mockup 3.png", 
        "Aqua - Mockup 4.png", 
        "Aqua - Mockup 5.png"
      ],
      [
        "Fly Away - Mockup 1.png",
        "Fly Away - Mockup 2.png",
        "Fly Away - Mockup 1.png",
        "Fly Away - Mockup 2.png",
      ],
      [
        "Climate Challenges - Mockup 1.png",
        "Climate Challenges - Mockup 2.png",
        "Climate Challenges - Mockup 3.png",
        "Climate Challenges - Mockup 1.png",
        "Climate Challenges - Mockup 2.png",
        "Climate Challenges - Mockup 3.png",
      ],
      [
        "Super Indie Run - Mockup 1.png",
        "Super Indie Run - Mockup 2.png",
        "Super Indie Run - Mockup 3.png",
        "Super Indie Run - Mockup 1.png",
        "Super Indie Run - Mockup 2.png",
        "Super Indie Run - Mockup 3.png",
      ],
      [
        "Bruin Dining - Mockup 1.png",
        "Bruin Dining - Mockup 1.png",
        "Bruin Dining - Mockup 1.png",
        "Bruin Dining - Mockup 1.png"
      ],
      [
        "Color Match - Mockup 1.png",
        "Color Match - Mockup 2.png",
        "Color Match - Mockup 3.png",
        "Color Match - Mockup 1.png",
        "Color Match - Mockup 2.png",
        "Color Match - Mockup 3.png",
      ],
      [
        "EZCalc - Mockup 1.png",
        "EZCalc - Mockup 1.png",
        "EZCalc - Mockup 1.png",
        "EZCalc - Mockup 1.png",
      ],
      [

      ],
      [
        "OI Treningsdagbok - Mockup 1.png",
        "OI Treningsdagbok - Mockup 2.png",
        "OI Treningsdagbok - Mockup 3.png",
        "OI Treningsdagbok - Mockup 1.png",
        "OI Treningsdagbok - Mockup 2.png",
        "OI Treningsdagbok - Mockup 3.png",
      ]
    ]

    const allProjectNames = [
      "",
      "Aqua",
      "Fly Away",
      "Climate Challenges",
      "Super Indie Run",
      "Bruin Dining",
      "Color Match",
      "EZCalc",
      "Magic Clipboard",
      "OI Treningsdagbok"
    ]
  
    const allProjectDescriptions = [
      "",
      "Aqua is an iOS app that displays data from Norwegian swimming meets in an easily understandable format. It also offers a multitude of features that go hand in hand with your needs while attending a swim meet such as qualification times for important events, a FINA-points calculator, and access to historic meet data.",
      "Fly Away is a fast-paced iOS game that requires you to be on your toes, constantly ready to react quickly to unexpected situations. Your goal is to pilot the rocket to avoid the falling meteors, while also trying to get as many coins as possible in order to purchase upgrades in the lab.",
      "Climate challenges is an iOS app that was ceated with the goal of encouraging people to act in a more environmentally responsible way. By offering a set of tasks associated with a points rewards, users can compete against each other to achieve the highest amount of points. In addition to making people act responsibly, the app is designed to educate people about how they can make an impact on the environment through simple changes in their day-to-day life. Full project here. Link to TestFlight download",
      "Super Indie Run is an iOS platformer game built using Swift's SpriteKit. The goal is to collect coins and stars while moving forwards in the level - but careful! There are cacti lurking around. Additional features include power-ups and the ability to double-jump. This game was built following a Udemy tutorial.",
      "Tired of forgetting when the dining halls are open? Or maybe you've ended up at a closed dining hall one too many times. Anyways, here's the solution: this iOS app allows you to add a widget to your phone that displays the dining halls that are currently open. Useful, right?",
      "Color Match is a very simple iOS game I made with the purpose of teaching myself SwiftUI. The principle is simple: the game picks a color at random and you have to recreate it by moving the red, green and blue sliders at the bottom of the screen. You will get a score depending on how close you got to the actual color.",
      "EZCalc is an iOS calculator that I built in UIKit. It is a basic calculator with a simple layout, similar to that of the iPhone's built-in calculator. However the feature that makes this app more practical is that you can see where your result originates from (see mockups). This means that instead of being shown only a result, you also see what you typed (and potentially discover any misclicks before it's too late).",
      "This simple iOS app allows you to use your phone's camera to copy items to your clipboard. It recognizes text using Machine Learning and by simply clicking a button the text visible to the camera will be instantly copied.",
      "OI Treningsdagbok is an iOS training diary I created to track my own workouts. The key features are a simple and ergonomic user interface, detailed insights into your training and built-in communication features for coaches."
    ]

    const keyWords = [
      "",
      "Keywords: Swift, Xcode, JSON, iOS, WebKit, Firebase, Firestore, iOS",
      "Keywords: Swift, Xcode, Firebase, Firestore, GameKit, StoreKit, Google Mobile Ads, SpriteKit, iOS",
      "Keywords: Swift, SwiftUI, Xcode, Firebase, Firestore, iOS",
      "Keywords: Swift, Xcode, SpriteKit, Game, iOS",
      "Keywords: Swift, Xcode, WidgetKit, iOS, UCLA",
      "Keywords: Swift, SwiftUI, Xcode, Game, iOS",
      "Keywords: Swift, UIKit, Xcode, iOS, Productivity",
      "Keywords: Swift, SwiftUI, Xcode, iOS, CameraKit, Machine Learning",
      "Keywords: Swift, UIKit, Xcode, iOS, Firebase, Firestore, productivity"
    ]

    let mockups = []
    for (const image of allProjectMockups[props.selected]) {
      mockups.push(images[image])
    }

    return (
      <div class="ProjectDetailContainer" id="projects">
        <div class="ProjectDescriptionContainer" >
          <h2 class="ProjectTitle">{allProjectNames[props.selected]}</h2>
          <p class="ProjectDescription">{allProjectDescriptions[props.selected]}</p>
          <h5 style={{opacity: 0.7, textAlign: "left", marginTop: "10px"}}>{keyWords[props.selected]}</h5>
        </div>
        <div class="MockupCarousel">
          <div class="container">
            <ImageSlider class="ProjectMockup" images={mockups}/>
          </div>
        </div>
      </div>
    )
  }
  

  const ProjectRow = (props) => {
    
    const showProject = (tag) => { // Make the project associated with the tag appear
      setSelectedProject(tag)
    }

    return (
      <button onClick={() => showProject(props.tag)}>
         <div class="projectRow" role="button">
          <div class="projectRowPart">
            <p>{props.tag}</p>
            <h3 class="projectName">{props.name}</h3>
          </div>
          <img class="projectIcon" src={images[props.imageName]} alt="icon"></img>
        </div>
      </button>
    );
  }

  return (
    <div class="projectsContainer" id="projectDetail">
        <div class="projectsDetail">
          <ProjectDetail selected={selectedProject}/>
        </div>
        <div class="projectsList" id="projectList">
          <h2>PROJECTS</h2>
          <div class="largeSeparator"/>
          <ProjectRow name="Aqua" imageName="Aqua - App icon.png" tag="1"/>
          <ProjectRow name="Fly Away" imageName="Fly Away - App icon.png" tag="2"/>
          <ProjectRow name="Climate Challenges" imageName="Climate Challenges - App icon.png" tag="3"/>
          <ProjectRow name="Super Indie Run" imageName="Super Indie Run - App icon.png" tag="4"/>
          <ProjectRow name="Bruin Dining" imageName="Bruin Dining - App icon.png" tag="5"/>
          <ProjectRow name="Color Match" imageName="Color Match - App icon.png" tag="6"/>
          <ProjectRow name="EZCalc" imageName="EZCalc - App icon.png" tag="7"/>
          <ProjectRow name="Magic Clipboard" imageName="Magic Clipboard - App icon.png" tag="8"/>
          <ProjectRow name="OI Treningsdagbok" imageName="OI Treningsdagbok - App icon.png" tag="9"/>
        </div>
      </div>
  )

}

export default ProjectsSection;